//https://github.com/TargetProcess/replayTable#presets
export default {
    "WinsLosses": {
        inputType: 'listOfMatches',
        terms: {
            'total': 'Win %'
        },
        totalValue: 'win %',
        resultMapping: {
            1: 'win',
            0: 'loss'
        },
        calculatedColumns: {
            'rounds': 'G',
            'wins': 'W',
            'losses': 'L'
        }
    },

    "F1": {
        inputType: 'pointsTable',
        terms: {
            'round': 'Race',
            'item': 'Driver'
        },
        addStartRound: 'Start ',
        resultMapping: {
            25: 'win'
        }
    },

    "": {
        inputType: 'pointsTable',
        terms: {
            'season': '',
            'round': '',
            'changes': '',
            'position': '',
            'item': '',
            'total': '',
            'change': ''
        },
        positionWhenTied: 'range',
        resultMapping: {
            1: 'win',
            0: ' '
        }
    }
};



